import Button from "@material-ui/core/Button";
import React, {useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import {PhotoCamera} from "@material-ui/icons";
import Input from "@material-ui/core/Input";
import CircularProgress from "@material-ui/core/CircularProgress";
import Box from "@material-ui/core/Box";
import Avatar from "@material-ui/core/Avatar";
import {defaultProfilePic} from "../../../constants/constants";
import Grid from "@material-ui/core/Grid";
import Link from "@material-ui/core/Link";
import {login} from "../../../constants/pagesurls";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Slider from "@material-ui/core/Slider";

const useStyles = makeStyles((theme) => ({
    root: {
        marginBottom: theme.spacing(2),
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    form: {
        width: '100%',
    },
    button: {
        marginRight: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },

}));


const CarForm = ({
                     name, setName, totSeats, setTotSeats,
                     consumption, setConsumption,
                     fuel, setFuel,
                 }) => {
    const classes = useStyles();
    const [nameError, setNameError] = useState(true)

    return (
        <div className={classes.root}>
            <form className={classes.form}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                        <TextField variant="outlined" fullWidth id="car-name" label="Name" placeholder="Fiat Uno 1992"
                                   value={name}
                                   onChange={(input) => {
                                       let name = input.target.value;
                                       if (name.length > 0) {
                                           setNameError(false)
                                       } else {
                                           setNameError(true)
                                       }
                                       setName(name);
                                   }}
                                   onBlur={(input) => {
                                       if (input.target.value.length < 0) {
                                           setNameError(true)
                                       }
                                   }}
                                   error={nameError}
                                   helperText="Name is required"
                                   autoComplete="name"/>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Select id="tot-seats" label="Seats" value={totSeats} defaultValue={4}>
                            <MenuItem value={2}>2</MenuItem>
                            <MenuItem value={3}>3</MenuItem>
                            <MenuItem value={4}>4</MenuItem>
                            <MenuItem value={5}>5</MenuItem>
                            <MenuItem value={6}>6</MenuItem>
                            <MenuItem value={7}>7</MenuItem>
                            <MenuItem value={8}>8</MenuItem>
                            <MenuItem value={9}>9</MenuItem>
                        </Select>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Select id="fuel" label="Fuel" value={fuel} defaultValue={1}>
                            <MenuItem value={1}>Petrol</MenuItem>
                            <MenuItem value={2}>Diesel</MenuItem>
                            <MenuItem value={3}>Gas</MenuItem>
                            <MenuItem value={4}>Electric</MenuItem>
                        </Select>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Slider
                            defaultValue={10}
                            getAriaValueText={(value) => (`${value} l/100km`)}
                            label="Consumption"
                            min={5}
                            max={20}
                            marks={[{value: 5, label: '5 l/100Km'}, {value: 25, label: '25 l/100Km'}]}
                            value={consumption}
                        />
                    </Grid>

                </Grid>
            </form>
        </div>
    )
};

export default CarForm;