import React, {useState} from "react";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from "@material-ui/core/styles";
import {white} from "color-name";
import Divider from "@material-ui/core/Divider";
import {Stepper} from "@material-ui/core";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import PersonalInfoForm from "./signup/PersonalInfoForm";
import StepContent from "@material-ui/core/StepContent";
import CarForm from "./signup/CarForm";


const useStyles = makeStyles((theme) => ({
    title: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(1),
    },
    button: {
        marginRight: theme.spacing(1),
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    root: {
        width: '95%',
        margin: 10
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },

}));

const getSteps = () => (
    ["Personal Info", "Add your Car", "Submit!"]
);


export default function SignupComponent(props) {
    const [email, setEmail] = useState("");
    const [emailError, setEmailError] = useState(false);
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [username, setUsername] = useState("");
    const [usernameError, setUsernameError] = useState(false);
    const [password, setPassword] = useState("");
    const [passwordError, setPasswordError] = useState(false);
    const [image, setImage] = useState(null);
    const [imageURL, setImageURL] = useState("");


    const [carName, setCarName] = useState("");
    const [totSeats, setTotSeats] = useState(4);
    const [consumption, setConsumption] = useState(10);
    const [fuel, setFuel] = useState(1);
    const getStepContent = (step) => {
        switch (step) {
            case 0:
                return <PersonalInfoForm firstName={firstName} setFirstName={setFirstName} lastName={lastName}
                                         setLastName={setLastName} username={username} setUsername={setUsername}
                                         usernameError={usernameError} setUsernameError={setUsernameError} email={email}
                                         setEmail={setEmail} emailError={emailError} setEmailError={setEmailError}
                                         password={password} setPassword={setPassword} passwordError={passwordError}
                                         setPasswordError={setPasswordError} image={image} setImage={setImage}
                                         imageURL={imageURL} setImageURL={setImageURL}
                />;
            case 1:
                return <CarForm name={carName} setName={setCarName} totSeats={totSeats} setTotSeats={setTotSeats}
                                consumption={consumption} setConsumption={setConsumption} fuel={fuel}
                                setFuel={setFuel}/>
            case 2:
                return "All submitted data";
            default:
                return "Unknown step";
        }
    };


    const steps = getSteps();
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const [skipped, setSkipped] = React.useState(new Set());

    const isStepValid = (step) => {
        switch (step) {
            case 0:
                return !emailError && email.length > 0 && !usernameError && username.length > 0 && !passwordError && password.length > 0;
            case 1:
                return true;
            case 2:
                return true;
        }
    };

    const isStepOptional = (step) => {
        return step === 1;
    };

    const isStepSkipped = (step) => {
        return skipped.has(step);
    };

    const handleNext = () => {
        let newSkipped = skipped;
        if (isStepSkipped(activeStep)) {
            newSkipped = new Set(newSkipped.values());
            newSkipped.delete(activeStep);
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped(newSkipped);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleSkip = () => {
        if (!isStepOptional(activeStep)) {
            // You probably want to guard against something like this,
            // it should never occur unless someone's actively trying to break something.
            throw new Error("You can't skip a step that isn't optional.");
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped((prevSkipped) => {
            const newSkipped = new Set(prevSkipped.values());
            newSkipped.add(activeStep);
            return newSkipped;
        });
    };

    const disabled = !isStepValid(activeStep);
    return (
        <div className={classes.root}>
            <Typography variant="h5" align="center" className={classes.title}>
                Sign Up!
            </Typography>
            <Divider/>
            <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((label, index) => {
                    const stepProps = {};
                    const labelProps = {};
                    if (isStepOptional(index)) {
                        labelProps.optional = <Typography variant="caption">Optional</Typography>;
                    }
                    if (isStepSkipped(index)) {
                        stepProps.completed = false;
                    }
                    return (
                        <Step key={label} {...stepProps}>
                            <StepLabel {...labelProps}>{label}</StepLabel>
                            <StepContent>
                                {getStepContent(index)}
                                <div>
                                    <Button disabled={activeStep === 0} onClick={handleBack} className={classes.button}>
                                        Back
                                    </Button>
                                    {isStepOptional(activeStep) && (
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            onClick={handleSkip}
                                            className={classes.button}
                                        >
                                            Skip
                                        </Button>
                                    )}

                                    <Button
                                        variant="contained"
                                        color="primary"
                                        onClick={handleNext}
                                        className={classes.button}
                                        disabled={disabled}
                                    >
                                        {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                                    </Button>
                                </div>
                            </StepContent>
                        </Step>
                    );
                })}
            </Stepper>
            {/*<Stepper activeStep={activeStep} orientation="vertical" className={classes.sectionMobile}>*/}
            {/*    {steps.map((label, index) => {*/}
            {/*        const stepProps = {};*/}
            {/*        const labelProps = {};*/}
            {/*        if (isStepOptional(index)) {*/}
            {/*            labelProps.optional = <Typography variant="caption">Optional</Typography>;*/}
            {/*        }*/}
            {/*        if (isStepSkipped(index)) {*/}
            {/*            stepProps.completed = false;*/}
            {/*        }*/}
            {/*        return (*/}
            {/*            <Step key={label} {...stepProps}>*/}
            {/*                <StepLabel {...labelProps}>{label}</StepLabel>*/}
            {/*            </Step>*/}
            {/*        );*/}
            {/*    })}*/}
            {/*</Stepper>*/}

            {activeStep === steps.length &&
            <>
                <Typography className={classes.instructions}>
                    All steps completed - you're finished
                </Typography>
            </>
            }

        </div>
    )
}