import * as actionTypes from "./types";


export const addAlert = (text, style) => {
    return {
        type: actionTypes.ADD_ALERT,
        text: text,
        style: style
    };
};

export const removeAllAlerts = () => (dispatch) => {
    dispatch({
        type: actionTypes.REMOVE_ALL_ALERTS
    });
};

export const removeAlert = (id) => (dispatch) => {
    dispatch({
        type: actionTypes.REMOVE_ALERT,
        id: id
    });
};


export const alertError = (error) => (dispatch) => {
    if (error.response) {
        dispatch(addAlert(error.response.data, "danger"));
    } else if (error.request) {
        dispatch(addAlert(error.request, "danger"))
    } else {
        dispatch(addAlert(error.message, "danger"));
    }
};

