import {notificationEditURL, notificationListURL} from "../constants/apiurls";
import {headers} from "../utils";
import {alertError} from "./alertActions";
import {AxiosInstance as axios} from "axios";
import {
    GET_NOTIFICATIONS_SUCCESS,
    NOTIFICATION_UPDATE,
    NOTIFICATIONS_ERROR,
    NOTIFICATIONS_START,
    CLEAR_NOTIFICATIONS
} from "../actions/types";

const start = () => (
    {
        type: NOTIFICATIONS_START
    }
);

const fail = () => (
    {
        type: NOTIFICATIONS_ERROR
    }
);

const getSuccess = (notifications) => (
    {
        type: GET_NOTIFICATIONS_SUCCESS,
        notifications
    }
);

/**
 *
 * @param id
 * @param {boolean} read
 */
const readSuccess = (id, read) => (
    {
        type: NOTIFICATION_UPDATE,
        id,
        read
    }
);


export const clearNotifications = () => (dispatch) => {
    dispatch({type: CLEAR_NOTIFICATIONS})
};

export const retrieveNotifications = () => (dispatch) => {
    dispatch(start());
    let access_token = localStorage.getItem("access_token");
    return axios
        .get(
            notificationListURL(),
            headers('application/json', access_token)
        )
        .then(res => {
            dispatch(getSuccess(res.data))
        })
        .catch(error => {
            alertError(error);
            dispatch(fail());
            return error;
        })
};

export const readNotification = (notificationId, read = true) => (dispatch) => {
    dispatch(start());
    let access_token = localStorage.getItem("access_token");
    return axios
        .put(
            notificationEditURL(notificationId),
            {
                read: read
            },
            headers('application/json', access_token)
        )
        .then(res => {
            dispatch(readSuccess(notificationId, read))
        })
        .catch(error => {
            alertError(error);
            dispatch(fail());
            return error;
        })
};

